const defaultModal = { image: '', imageAlt: '', show: false };

const Projects = props => {
	const [modal, setModal] = useState(defaultModal);

	useEffect(() => {
		printLetterByLetter(document.getElementById('title'), title, titleInterval);
		printingChars();
	}, []);

	return (
		<>
			<motion.div initial="initial" animate="in" exit="out" variants={props.pageVariants} transition={props.pageTransition}>
				<h1 id="title"></h1>
				{projects.map((project, index) => (
					<ProjectCard key={index} project={project} setModal={setModal} />
				))}

				<Link to="/">
					<button className="btn back-btn" style={{ marginBottom: 25, marginTop: 10 }}>
						Back
					</button>
				</Link>
			</motion.div>
			<Modal show={modal.show} image={modal.image} imageAlt={modal.imageAlt} onClose={() => setModal(defaultModal)} />
		</>
	);
};

export default Projects;
