import React, { Component } from 'react';
import { motion } from 'framer-motion';
import ProgressBar from '../components/ProgressBar';
import { printingChars, printLetterByLetter, titleInterval } from '../functions.js';
import { Link } from 'react-router-dom';

const title = 'My Skills';

export default class Skills extends Component {
	componentDidMount() {
		printLetterByLetter(document.getElementById('title'), title, titleInterval);
		printingChars();
	}

	componentDidUpdate() {
		window.scrollTo(0, 0);
	}

	render() {
		const skills = [
			{ name: 'JavaScript', l: 95, eg: 'TypeScript' },
			{ name: 'HTML/CSS', l: 80 },
			{ name: 'React', l: 80 },
			{ name: 'Next', l: 70 },
			{ name: 'Electron', l: 55 },
			{ name: 'Express', l: 85 },
			{ name: 'Databases', l: 65, eg: 'SQL/Mongo' },
			{ name: 'Python', l: 70 },
			{ name: 'Java', l: 40 },
			{ name: 'C', l: 35 },
			{ name: 'Go', l: 55 },
			{ name: 'Solidity', l: 65 },
		];
		return (
			<motion.div initial="initial" animate="in" exit="out" variants={this.props.pageVariants} transition={this.props.pageTransition}>
				{/* eslint-disable-next-line */}
				<h1 id="title"></h1>
				<div className="main-content">
					<div className="left-column">
						{skills.slice(0, Math.ceil(skills.length / 2)).map(i => (
							<ProgressBar name={i.name} key={i.name} limit={i.l} eg={i.eg} />
						))}
					</div>
					<div className="right-column">
						{skills.slice(Math.ceil(skills.length / 2)).map(i => (
							<ProgressBar name={i.name} key={i.name} limit={i.l} eg={i.eg} />
						))}
					</div>
				</div>

				<Link to="/">
					<button className="btn back-btn">Back</button>
				</Link>
			</motion.div>
		);
	}
}
